import React, { Fragment } from "react";
import { AccessControlTypes, FileAssociation } from "types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function FileAssociationModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/fileAssociation", "create", {} as FileAssociation);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New File Association" />
      </ModalOpenButton>
      <ModalContent title="Create New File Association" onClickOk={handleOk} formName="create_fileassociation_form">
        <Form schemaName="fileAssociation">
          <FormContentBase
            name="create_fileassociation_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
