import React from "react";
import { CustomColumns, FileAssociation } from "../../types";
import { useQuery } from "react-query3";
import { PageHeader, Space } from "antd";
import { Modal } from "components/ui/Modal";
import Table from "components/ui/Table";
import TagLink from "components/ui/TagLink";
import EditModal from "components/ui/EditModal";
import DeleteButton from "components/ui/DeleteButton";
import FileAssociationsModal from "./fileAssociationsModal";
import DocsButton from "components/docsButton";

const columns: CustomColumns<FileAssociation>[] = [
  {
    title: "Extension",
    dataIndex: "extension",
    key: "extension",
    editable: false
  },
  {
    title: "Script",
    key: "script",
    editable: false,
    render: (text, record) => (
      <TagLink
        to={`/admin/automation/scripts/${record?.script}`}
        text={record?.script}
      />
    ),
  },

  {
    title: "Environment",
    key: "environment",
    dataIndex: "environment",
    editable: false,
    render: (text) => (text ? text : "Default"),
  },
  {
    key: "runAs",
    title: "Run As",
    editable: false,
    render: (text, record) => record?.credential || "Default",
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    editable: false,
    render: (text, record) => {
      return (
        <Space>
          <Modal>
            <EditModal record={record} allowedWithOneWayGitSync={false} />
          </Modal>
          <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
        </Space>
      );
    },
  },
];

export default function FileAssociations() {
  const { data: fileAssociations, isLoading, refetch } = useQuery<FileAssociation[]>(
    "/fileAssociation",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  return (
    <PageHeader title="File Associations" subTitle="Launch PowerShell scripts when files are opened." extra={[
      <DocsButton url="desktop/file-associations" />
    ]}>
      <Table
        title="File Associations"
        actions={[
          <Modal>
            <FileAssociationsModal />
          </Modal>,
        ]}
        columns={columns}
        data={fileAssociations}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>

  );
}
